import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import a from '@/components/HelloWorld'
import b from '@/components/HelloFromVux'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    { 
      path: '/login',
      component: login
    },
    {
      path: '/a',
      name: 'a',
      component: a
    },
    {
      path: '/b/:id',
      name: 'b',
      component: b
    }
  ]
})



